$headers: (
  (
    size: 95px,
    weight: 300,
    spacing: -1.5px,
  ),
  (
    size: 60px,
    weight: 300,
    spacing: -0.5px,
  ),
  (
    size: 48px,
    weight: 400,
    spacing: 0px,
  ),
  (
    size: 34px,
    weight: 400,
    spacing: -0.25px,
  ),
  (
    size: 24px,
    weight: 400,
    spacing: 0px,
  ),
  (
    size: 20px,
    weight: 500,
    spacing: 0.15px,
  )
);
@for $i from 1 through length($headers) {
  $map: nth($headers, $i);
  $size: map-get($map, "size");
  $weight: map-get($map, "weight");
  $spacing: map-get($map, "spacing");
  h#{$i} {
    font-size: $size;
    font-weight: $weight;
    letter-spacing: $spacing;
  }
  .fs-#{$i} {
    font-size: $size;
  }
}
p {
  font-size: 16px;
  letter-spacing: 0.5px;
  &.lg {
    font-size: 18px;
    letter-spacing: 0.15px;
  }
  &.sm {
    font-size: 14px;
    letter-spacing: 0.25px;
  }
}
.caption {
  font-size: 12px;
  letter-spacing: 0.4px;
}
.overline {
  font-size: 10px;
  letter-spacing: 1.5px;
}
