$spacer: 1rem;
$spacers: (0, 0.25, 0.5, 1, 1.5, 3);
$properties: (
  p: padding,
  m: margin,
);
$sides: (
  t: (
    top,
  ),
  l: (
    left,
  ),
  r: (
    right,
  ),
  b: (
    bottom,
  ),
  x: (
    left,
    right,
  ),
  y: (
    top,
    bottom,
  ),
);

@for $i from 0 to length($spacers) {
  $space: nth($spacers, $i + 1);
  @each $class, $prop in $properties {
    $name: $class + "-" + $i;
    .#{$name} {
      @if $space != 0 {
        #{$prop}: calc($space * $spacer);
      } @else {
        #{$prop}: 0px;
      }
    }
    .#{$class}-auto {
      #{$prop}: auto;
    }
  }
  @each $sideKey, $sideList in $sides {
    @each $class, $prop in $properties {
      @each $side in $sideList {
        $name: $class + $sideKey + "-" + $i;
        $sideName: $prop + "-" + $side;
        .#{$name} {
          @if $space != 0 {
            #{$sideName}: calc($space * $spacer);
          } @else {
            #{$sideName}: 0px;
          }
        }
        .#{$class + $sideKey}-auto {
          #{$sideName}: auto;
        }
      }
    }
  }
}
