*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Public Sans";
}
html,
body {
  margin: 0;
}
body {
  background-color: #fff;
}
a {
  text-decoration: none;
  color: inherit;
  &:link,
  &:active,
  &:visited {
    color: inherit;
  }
}
img {
  display: block;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
button {
  cursor: pointer;
}
@for $i from 1 through 6 {
  h#{$i} {
    margin: {
      top: 0;
      bottom: 0.5rem;
    }
  }
}
