@import "./general";
@import "./colors";
@import "./utilities";
@import "./fonts";
@import "./spacing";
@import "./sizes";
@import "./flex";
@import "./display";
:root {
  --navbar-height: 60px;
  --section-padding: 3rem;
}
// Navigation
#navbar {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: $white;

  .navbar__item {
    position: relative;
    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 3px;
      background-color: $lime-green;
      opacity: 0;
      transition: 250ms ease-in-out;
    }
    &:hover::before {
      opacity: 0.2;
    }
    &.active::before {
      opacity: 1;
    }
  }
  .btn-menu {
    display: none;
    &.active {
      .menu {
        display: none;
      }
    }
    &:not(.active) {
      .close {
        display: none;
      }
    }
  }
}
//  Sections
.section {
  min-height: calc(100vh - var(--navbar-height));
  width: 100%;
  padding: var(--section-padding) 0;
  &:nth-child(2n + 1) {
    background-color: $light-grayish-blue;
  }
  &:nth-child(2n) {
    background-color: $very-light-gray;
  }
}
// Intro
.intro {
  &__container {
    position: relative;
    height: 100%;
  }
  position: relative;
  &__wrapper {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
  }
  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    &--front {
      width: 80%;
      height: calc(100% + (var(--section-padding) * 3));
      background: {
        image: url(../asset/images/image-mockups.png);
        size: auto 120%;
        position: left bottom;
        repeat: no-repeat;
      }
    }
    &--back {
      background: {
        image: url(../asset/images/bg-intro-desktop.svg);
        size: auto 110%;
        position: left 200%;
        repeat: no-repeat;
      }
    }
  }
}
// Grid for containers
.benefits,
.articles {
  &__container {
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.6rem;
  }
}
// Benefits
.benefits {
  display: flex;
  align-items: center;
  .subheader,
  .description {
    line-height: 1.8;
  }
  .header {
    margin-bottom: 2rem;
  }
  .subheader {
    max-width: 650px;
    margin-bottom: 5rem;
  }
  &__card {
    width: 100%;
    h5 {
      margin: 2rem 0;
    }
  }
}
// Articles
.articles {
  display: flex;
  align-items: center;
  &__card {
    width: 100%;
    background-color: $white;
    img {
      width: 100%;
      height: 200px;
      object-fit: cover;
      object-position: top center;
    }
  }
}

footer {
  background-color: $dark-blue;
  padding: var(--section-padding) 0;
  .social-media {
    a {
      display: block;
      padding: 0.2rem 0.5rem;
    }
  }
  .footer-links {
    display: grid;
    grid-template-columns: repeat(2, 200px);
    align-items: center;
    padding: 0 5rem;
    flex-grow: 1;
    a {
      width: 100%;
      color: $white;
    }
  }
}

@media screen and (max-width: 1150px) {
  // Grid for containers
  .benefits,
  .articles {
    &__container {
      display: grid;
      justify-content: space-between;
      grid-template-columns: repeat(2, 1fr);
      gap: 1.6rem;
    }
  }
}
// media for phones
@media screen and (max-width: 750px) {
  .section {
    text-align: center;
  }
  // Intro
  .intro {
    display: flex;
    flex-direction: column;
    padding-top: 0px;
    &__container {
      padding: {
        left: 2rem;
        right: 2rem;
      }
      & > * {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
      }
    }

    &__wrapper {
      position: static;
      width: 100%;
      height: 0%;
      flex-grow: 1;
      order: -1;
    }
    &__bg {
      position: static;
      &--front {
        width: 100%;
        height: 100%;
        background: {
          image: url(../asset/images/bg-intro-mobile.svg);
          size: cover;
          position: bottom right;
        }
      }
      &--back {
        display: none;
      }
    }
  }
  // Navigation
  #navbar {
    .navbar__list,
    .btn-request-invite {
      display: none;
    }
    .navbar__list {
      position: absolute;
      top: calc(100%);
      left: 0;
      width: 100%;
      height: calc(100vh - 3rem);
      padding: 0 {
        top: 2rem;
        bottom: 4rem;
      }
      background-image: linear-gradient(to bottom, $dark-blue, transparent);
    }
    .navbar__item {
      width: 100%;
      max-width: 400px;
      margin: {
        left: auto;
        right: auto;
      }
      background-color: $white;
      &::before {
        display: none;
      }
      &:hover {
        opacity: transparentize($lime-green, 0.8);
      }
      &.active {
        color: $lime-green;
      }
    }

    .btn-menu {
      display: block;
      &:focus + .navbar__list {
        display: block;
      }
    }
  }
  // footer
  footer {
    & > * {
      flex-direction: column;
    }
    .logo {
      display: flex;
      justify-content: center;
    }
    .social-media {
      justify-content: center;
      margin: 2rem 0;
    }
    .footer-links {
      align-items: center;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      padding: 1rem 0;
      gap: 1rem 0;
      text-align: center;
      & + div {
        align-items: center;
        & > * {
          margin: 1rem 0;
        }
      }
    }
  }
}
@media screen and (max-width: 500px) {
  // Grid for containers
  .benefits,
  .articles {
    &__container {
      display: grid;
      justify-content: space-between;
      grid-template-columns: 1fr;
      gap: 1.6rem;
    }
  }
}
