@function str-split($string, $separator) {
  $split-arr: ();
  $index: str-index($string, $separator);
  @while $index != null {
    $item: str-slice($string, 1, $index - 1);
    $split-arr: append($split-arr, $item);
    $string: str-slice($string, $index + 1);
    $index: str-index($string, $separator);
  }
  $split-arr: append($split-arr, $string);

  @return $split-arr;
}
@function getFirstLetter($string, $separator) {
  $strings: str-split($string, $separator);
  $letters: ();
  $str: "";
  @each $s in $strings {
    $str: $str + str-slice($s, 1, 1);
  }

  @return $str;
}
$directions: (row, column, column-reverse, row-reverse);
$props: (justify-content, align-items);
$sides: (
  flex-start,
  center,
  flex-end,
  space-between,
  space-evenly,
  space-around
);
// flex direction class
@each $direct in $directions {
  .fd#{getFirstLetter($direct,"-")} {
    flex-direction: $direct;
  }
}
// flex positions
@each $prop in $props {
  @each $side in $sides {
    $firstLetters: getFirstLetter($prop, "-") + getFirstLetter($side, "-");
    .#{$firstLetters} {
      #{$prop}: $side;
    }
  }
}
