@import "./colors";
$text-colors: (
  dark-blue: $dark-blue,
  lime-green: $lime-green,
  bright-cyan: $bright-cyan,
  grayish-blue: $grayish-blue,
  light-grayish-blue: $light-grayish-blue,
  very-light-gray: $very-light-gray,
);

@each $key, $value in $text-colors {
  .text-#{$key} {
    color: #{$value};
  }
}
.text-black {
  color: white;
}
.text-white {
  color: white;
}
.btn {
  background-color: transparent;
  border: none;
  padding: 12px 24px;
  font-size: 16px;

  &--primary {
    background-image: linear-gradient(90deg, $lime-green, $bright-cyan);
    border-radius: 24px;
    color: white;
  }
}
.container {
  width: 100%;
  margin: {
    left: auto;
    right: auto;
  }
  padding: {
    left: 1rem;
    right: 1rem;
  }
  &-fluid {
    width: 100%;
  }
  &-xxl,
  &-xl,
  &-lg,
  &-md,
  &-sm {
    @extend .container;
  }
}
@media screen and (max-width: 576px) {
  .container {
    &-xxl,
    &-xl,
    &-lg,
    &-md,
    &-sm {
      max-width: 100%;
    }
  }
}
@media screen and (min-width: 576px) {
  .container {
    max-width: 540px;
    &-xxl,
    &-xl,
    &-lg,
    &-md {
      max-width: 100%;
    }
    &-sm {
      max-width: 540px;
    }
  }
}
@media screen and (min-width: 768px) {
  .container {
    &-xxl,
    &-xl,
    &-lg {
      max-width: 100%;
    }
    max-width: 720px;
    &-md,
    &-sm {
      max-width: 720px;
    }
  }
}
@media screen and (min-width: 992px) {
  .container {
    &-xxl,
    &-xl {
      max-width: 100%;
    }
    max-width: 960px;
    &-lg,
    &-md,
    &-sm {
      max-width: 960px;
    }
  }
}
@media screen and (min-width: 1200px) {
  .container {
    &-xxl {
      max-width: 100%;
    }
    max-width: 1140px;
    &-xl,
    &-lg,
    &-md,
    &-sm {
      max-width: 1140px;
    }
  }
}
@media screen and (min-width: 1440px) {
  .container {
    max-width: 1320px;
    &-xxl,
    &-xl,
    &-lg,
    &-md,
    &-sm {
      max-width: 1320px;
    }
  }
}
